import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import { GET_MY_ADDRESS_START, GET_MY_ADDRESS_SUCCESS, GET_MY_ADDRESS_ERROR } from '../../constants';

const getMyAddressStart = () => {
    return { type: GET_MY_ADDRESS_START };
};
const getMyAddressSuccess = (payload) => {
    return { type: GET_MY_ADDRESS_SUCCESS, payload };
};
const getMyAddressError = (error) => {
    return { type: GET_MY_ADDRESS_ERROR, error };
};

const getMyAddress = () => (dispatch) => {
    dispatch(getMyAddressStart());
    try {
        http(process.env.REACT_APP_ROOT_URL, 'GET', `users-service/users/addresses?pageNumber=1&pageSize=10`, true)
            .then((res) => {
                const payload = {
                    myAddressData: res
                };
                dispatch(getMyAddressSuccess(payload));
            })
            .catch((err) => {
                dispatch(getMyAddressError(err));
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        dispatch(getMyAddressError('Something went wrong'));
    }
};
const addMyAddress = (payload, successCb) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL, 'POST', `users-service/users/addresses`, payload, true)
            .then((res) => {
                successCb && successCb(res);
            })
            .catch((err) => {
                dispatch(setMessage(err?.message));
            });
    } catch (error) {
        dispatch(setMessage(error?.message));
    }
};
const updateMyAddress = (payload, addressId, successCb) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL, 'PUT', `users-service/users/addresses/${addressId}`, payload, true)
            .then((res) => {
                successCb && successCb(res?.message);
            })
            .catch((err) => {
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        dispatch(setMessage(error?.message));
    }
};
export { getMyAddress, addMyAddress, updateMyAddress };
