import React, { useState, useEffect } from 'react';
import { withStyles, useTheme } from '@material-ui/styles';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';
import GroupIcon from '@material-ui/icons/Group';
import useStyles from '../user-management/styles';
import {
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import { getMyAddress } from '../../store/actions/MyAddressActions';
import AddMyAddressModal from './AddMyAddress';
import SchoolAddress from './school-address';
import useUserRole from '../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function MyAddress() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN', 'TEACHER', 'STUDENT','SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [isOpenModal, setOpenModal] = useState(false);
    const [isEditData, setEditData] = useState(null);
    const { myAddressData } = useSelector((state) => state?.myAddress);
    const newData = myAddressData?.userAddresses?.content;
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };
    const handleUpdateAddress = (data) => {
        setEditData(data);
        setOpenModal(true);
    };
    const handleOpenModal = () => {
        setOpenModal(true);
    };
    const handleCloseModal = () => {
        setEditData(null);
        setOpenModal(false);
    };
    useEffect(() => {
        dispatch(getMyAddress());
    }, []);
    return (
        <>
            {(loggedUserProfile?.role === 'STUDENT' || loggedUserProfile?.role === 'SCHOOL_ADMIN') && <SchoolAddress />}
            <br />
            {loggedUserProfile?.role !== 'SCHOOL_ADMIN' &&  <>
            <div className={classes.titleContainer}>
                <GroupIcon className={classes.pageTitleIcon} />
                <span className={classes.pageTitle}>&nbsp; Home Address </span>
            </div>
            <Paper elevation={5} className={classes.root}>
                <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9' }}
                        onClick={() => handleOpenModal()}
                    >
                        <AddIcon /> Address
                    </Button>
                </div>
                <TableContainer
                    className={classes.tableContainer}
                    sx={{
                        height: 300
                    }}
                >
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead className={classes.tableHeaderCell}>
                            <StyledTableRow>
                                <TableCell>Address</TableCell>
                                <TableCell>State</TableCell>
                                <TableCell>City</TableCell>
                                <TableCell>Country</TableCell>
                                <TableCell>Pincode</TableCell>
                                <TableCell>Landmark</TableCell>
                                <TableCell align="center"> Default </TableCell>
                                <TableCell align="center"> Action </TableCell>
                            </StyledTableRow>
                        </TableHead>
                        {!newData ? (
                            <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                        ) : (
                            <TableBody>
                                {newData?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)?.map((row) => (
                                    <StyledTableRow key={row?.id}>
                                        <TableCell>
                                            {row?.house}, {row?.street}
                                        </TableCell>
                                        <TableCell>{row?.state || '-'}</TableCell>
                                        <TableCell>{row?.city || '-'}</TableCell>
                                        <TableCell>{row?.country || '-'}</TableCell>
                                        <TableCell>{row?.pincode || '-'}</TableCell>
                                        <TableCell>{row?.landmark || '-'}</TableCell>
                                        <TableCell align="center">{row?.default === true ? 'Yes' : '-'}</TableCell>
                                        <TableCell align="center">
                                            <Tooltip title="Edit address">
                                                <IconButton style={{ cursor: 'pointer' }} onClick={() => handleUpdateAddress(row)}>
                                                    <EditIcon color="primary" style={{ cursor: 'pointer' }} />
                                                </IconButton>
                                            </Tooltip>
                                        </TableCell>
                                    </StyledTableRow>
                                ))}
                            </TableBody>
                        )}
                    </Table>
                </TableContainer>
                {/* <TablePagination
                    rowsPerPageOptions={[5, 10, 15, 25]}
                    component="div"
                    count={newData?.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onPageChange={handleChangePage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                    sx={{
                        '.MuiTablePagination-displayedRows': {
                            marginBottom: 0
                        },
                        '.MuiTablePagination-selectLabel': {
                            marginBottom: 0
                        }
                    }}
                /> */}
            </Paper>
            </>}
            {isOpenModal && <AddMyAddressModal open={isOpenModal} onClose={handleCloseModal} isEditData={isEditData} />}
        </>
    );
}
