import React, { useEffect, useMemo } from 'react';
import CloseIcon from '@material-ui/icons/Close';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider, RHFTextField, RHFCheckbox } from '../../components/hook-form';
import useStyles from '../user-management/styles';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { addMyAddress, getMyAddress, updateMyAddress } from '../../store/actions/MyAddressActions';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250
        }
    }
};

const addAddressSchema = yup.object({
    pincode: yup.string('Enter pincode').required('Pincode is required')
});
export default function AddMyAddressModal(props) {
    const { open, onClose, isEditData } = props;
    const dispatch = useDispatch();
    const classes = useStyles();

    const initialValues = useMemo(
        () => ({
            house: isEditData?.house || '',
            street: isEditData?.street || '',
            landmark: isEditData?.landmark || '',
            pincode: isEditData?.pincode || '',
            default: isEditData?.default || ''
        }),
        [isEditData]
    );
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(addAddressSchema),
        defaultValues: initialValues
    });
    const { reset, handleSubmit } = methods;
    useEffect(() => {
        if (!isEditData) {
            reset(initialValues);
        }
    }, [isEditData]);
    const onSubmit = (data) => {
        const payload = {
            house: data?.house,
            street: data?.street,
            landmark: data?.landmark,
            pincode: data?.pincode,
            default: data?.default
        };
        if (isEditData) {
            dispatch(
                updateMyAddress(payload, isEditData?.id, () => {
                    dispatch(getMyAddress());
                    onClose();
                })
            );
        } else {
            dispatch(
                addMyAddress(payload, () => {
                    dispatch(getMyAddress());
                    onClose();
                })
            );
        }
    };

    return (
        <div>
            <Dialog open={open} maxWidth="xs" classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}>{isEditData ? 'Update' : 'Add'} Address</Typography>
                        <Link to="#" onClick={onClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <DialogContent>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    House<span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="house" placeholder="Enter house" className={classes.loginInput} autoFocus />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    Street<span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="street" placeholder="Enter street" className={classes.loginInput} />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}> Landmark</label>
                                <RHFTextField name="landmark" placeholder="Enter landmark" className={classes.loginInput} />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <label className={classes.label}>
                                    Pincode<span className={classes.mandatory}>*</span>
                                </label>
                                <RHFTextField name="pincode" placeholder="Enter pincode" className={classes.loginInput} />
                            </Grid>

                            <Grid item xs={12} sm={12}>
                                <RHFCheckbox name="default" label="Default" />
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="primary" className={classes.buttonCancel} onClick={onClose}>
                            Close
                        </Button>
                        <Button variant="contained" color="primary" type="submit" className={classes.buttonSubmit}>
                            {isEditData ? 'Update' : 'Add'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
