import React, { useState, useEffect } from 'react';
import { withStyles, useTheme } from '@material-ui/styles';
import GroupIcon from '@material-ui/icons/Group';
import useStyles from '../../user-management/styles';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Checkbox } from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { getMyAddress } from '../../../store/actions/MyAddressActions';
import { setMessage } from '../../../store/actions/MessageActions';
import { schoolAddressUpdateDefault } from '../../../services/School';
import useUserRole from '../../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function SchoolAddress() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN', 'TEACHER', 'STUDENT','SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);

    const { myAddressData } = useSelector((state) => state?.myAddress);
    const [checked, setChecked] = React.useState(false);

    const newData = myAddressData?.schoolAddress;

    const handleChange = async (event) => {
        const payload = {
            addressType: 'SCHOOL',
            addressId: loggedUserProfile.schoolId
        };

        const res = await schoolAddressUpdateDefault(loggedUserProfile.id, loggedUserProfile.profileId, payload);
        console.log('res--', res);
        dispatch(setMessage(res?.message ? res?.message : ''));
        dispatch(getMyAddress());
    };

    useEffect(() => {
        dispatch(getMyAddress());
    }, []);

    return (
        <>
            <div className={classes.titleContainer}>
                <GroupIcon className={classes.pageTitleIcon} />
                <span className={classes.pageTitle}>&nbsp; School Address </span>
            </div>
            <Paper elevation={5} className={classes.root}>
                <TableContainer
                    className={classes.tableContainer}
                    sx={{
                        height: 300
                    }}
                >
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead className={classes.tableHeaderCell}>
                            <StyledTableRow>
                                <TableCell> Name </TableCell>
                                <TableCell> Address </TableCell>
                                <TableCell> State </TableCell>
                                <TableCell> City </TableCell>
                                <TableCell> Country </TableCell>
                                <TableCell> Pincode </TableCell>
                                <TableCell> Branch </TableCell>
                                <TableCell align="center"> Default </TableCell>
                            </StyledTableRow>
                        </TableHead>
                        {!newData ? (
                            <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                        ) : (
                            <TableBody>
                                <StyledTableRow key="">
                                    <TableCell> {newData?.name} </TableCell>
                                    <TableCell> {newData?.address} </TableCell>
                                    <TableCell> {newData?.state} </TableCell>
                                    <TableCell> {newData?.city} </TableCell>
                                    <TableCell> {newData?.country} </TableCell>
                                    <TableCell> {newData?.pincode} </TableCell>
                                    <TableCell> {newData?.branch} </TableCell>
                                    <TableCell align="center">
                                        {newData?.default === true ? (
                                            'Yes'
                                        ) : (
                                            <Checkbox
                                                checked={checked}
                                                onChange={handleChange}
                                                inputProps={{ 'aria-label': 'controlled' }}
                                            />
                                        )}
                                    </TableCell>
                                </StyledTableRow>
                            </TableBody>
                        )}
                    </Table>
                </TableContainer>
            </Paper>
        </>
    );
}
